/*
  Reset and base code
*/
:root {
  box-sizing: border-box;
  --color-dark: #0C0C0C;
  --color-gray: #C6C6C6;
  --color-light-dark: #424040;
  --color-main: #2B2B35;
  --color-white: #fff;
  --color-pink: #CD628F;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  background-color: var(--color-dark);
  height: 100vh;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  margin: 0;
  overflow: hidden;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 900;
  letter-spacing: 1px;
  margin: 10px 0;
  text-transform: uppercase;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

/*
  Page code
*/

nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

nav img {
  width: 200px;
}

nav a {
  margin: 20px 20px 0 0;
}

main {
  display: flex;
  flex-flow: row nowrap;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.content-page {
  transform-style: preserve-3d;
  transition: all 400ms linear;
  z-index: 3;
}

.content-page.open {
  border: 20px solid var(--color-pink);
  box-shadow: 0px 2px 12px 3px rgba(0,0,0,0.75);
  cursor: pointer;
  transform: perspective(800px) rotateY(30deg) translateZ(-1200px);
}

.menu {
  display: flex;
  justify-content: flex-end;
  padding: 10% 10% 0 0;
  z-index: 2;
}

.menu ul li {
  margin: 20px 0;
  text-align: center;
}

.menu ul li:hover a {
  text-decoration: line-through;
  text-decoration-color: var(--color-pink);
}

.menu ul li a {
  color: var(--color-white);
  font-weight: 900;
  font-size: 3em;
  transform-style: preserve-3d;
}

.content-page,
.menu {
  background-color: var(--color-dark);
  height: 100vh;
  position: absolute;
  width: 100vw;
}

.container {
  height: 100vh;
  position: relative;
  width: 100vw;
}

.scrollbar {
  border-left: 1px solid var(--color-gray);
  color: var(--color-gray);
  display: flex;
  flex-direction: column;
  height: 50vh;
  margin-top: 20vh;
  padding-left: 20px;
  width: 20%;
}

.scrollpoint {
  align-items: center;
  display: flex;
  flex: 1;
  position: relative;
}

.scrollpoint span {
  cursor: pointer;
  position: absolute;
  transition: all 300ms ease-in;
}

.scrollpoint > span:nth-child(2) {
  visibility: hidden;
  transform: translateX(300%);
}

.scrollpoint.selected > span {
  font-weight: 900;
  left: -55px;
}

.scrollpoint.selected > span:nth-child(2) {
  visibility: visible;
  transform: translateX(50px);
}

.menuicon {
  color: var(--color-white);
  font-size: 2em;
}

.scrollpage {
  display: flex;
  height: 50vh;
  margin-top: 20vh;
  position: relative;
  width: 80%;
}

.scrollpage .slide {
  background-color: var(--color-dark);
  color: var(--color-white);
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  transition: all 300ms ease-in;
  width: 100%;
  z-index: 1;
}

.slide.active {
  opacity: 1;
  z-index: 5;
}

.slide-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
}

.slide-content h1 {
  font-size: 3em;
  max-width: 500px;
}

.slide1 .slide-content {
  background: url(../assets/warrior.png) no-repeat;
  background-position-x: 100%;
  background-size: contain;
}

.slide2 .slide-content {
  background: url(../assets/slide2.png) no-repeat;
  background-position-x: 100%;
  background-position-y: 40%;
  background-size: 40%;
}

.slide3 .slide-content {
  background: url(../assets/slide3.png) no-repeat;
  background-position-x: 100%;
  background-position-y: 50%;
  background-size: 40%;
}

.slide4 .slide-content {
  background: url(../assets/slide4.png) no-repeat;
  background-position-x: 100%;
  background-position-y: 50%;
  background-size: 50%;
}

.button-link {
  color: var(--color-white);
  font-weight: 700;
  max-width: 250px;
  position: relative;
  text-transform: uppercase;
  transition: all 300ms linear;
  z-index: 1;
}

.button-link span {
  padding-bottom: 10px;
  transition: all 300ms linear;
}

.button-link:hover {
  box-shadow: inset 0 0px 0 white, inset 0 -1px 0 var(--color-pink);
}

.button-link:hover span::after {
  opacity: 0;
  text-decoration: none;
}

.button-link span::after {
  content: 'â†’';
  font-size: 1.5em;
  font-weight: 900;
  margin-left: 10px;
  text-decoration: none;
  transition: all 300ms linear;
}